.contact-header {
    padding: 50px 0;
    background-color: #ececec;
}


.contact-container {
    display: flex;
    justify-content: center;
    font-family: inherit;
    background-color: #ececec;
}

.contact {
    width: 100%;
    max-width: 1114px;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    margin: 5vw 0;
}

.contact-form-container {
    width: 60%;
    display: flex;
    padding: 2.75rem;
    flex-direction: column;
    align-items: center;
    gap: 1.5vw;
    background-color: #ffffff;
    border-radius: 16px;
    box-shadow: 0 12px 10px 0 #00000007;
}

.contact-form-container h1 {
    font-family: inherit;
    font-size: clamp(1.3rem,6vw,2.5rem);
    font-weight: 400;
    margin: 0;
}

.contactform-title-text {
    margin: 0;
    text-align: center;
    font-size: clamp(12px,16px,18px);
    font-weight: 400;
    line-height: 37px;
    color: #5c4529;
}

.user-details {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    align-self: flex-start;
}


.contact-fields {
    width: 80%;
    height: 52px;
    padding: 0 1.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #ffffff;
    border: 1px solid #c4c4c4;
    border-radius: 0.5rem;
    font-size: 16px;
    font-family: inherit;
    color: #311f09;
}

.textarea {
    width: 100%;
    display: flex;
}

.contact-textarea-field {
    width: 80%;
    height: 100px;
    padding: 1.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #ffffff;
    border: 1px solid #c4c4c4;
    border-radius: 0.5rem;
    font-size: 16px;
    font-family: inherit;
    color: #311f09;
}


.contactform-errors {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    font-size: 14px;
    color: #f54748;
}

@media (max-width: 768px) {
    .checkout-form-container{
        transform: scale(0.9);
    }
    .user-field, .textarea-field {
        font-size: 12px;
    }
    
}