.footer {
    background-color: #1b1919;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 60px 0; /* was 70px */
    /* delete below */
/*     border: 2px solid red;
 */    flex-wrap:wrap;
}



.footer-container {
    max-width: 1114px;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    font-family: inherit;
    /* delete below */
/*     outline: 3px double green;
 */}

.footer-content {   /* Including copyright part */
    display: flex;
    flex-direction: column;
    align-items: stretch;
    gap: 60px;
    /* delete below */
   
}

.firstrow {
    display: flex;
    padding: 1rem; /* added later */
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
    /* delete below */
   
}

.footer-left {
    width: 32%;
    display: flex;
    flex-direction: column;
    gap: 42px;
    color: #e3e1df;
    /* delete below */
/*     outline: 2px dotted orange;
 */}


.footer-lefttext {
    margin: 0;
    color: #e3e1df;
    line-height: 200%;
    font-weight: 400;
    font-size: 20px;
}

.footer-icons {
    width: fit-content; /*  was 68% */
    display: flex;
    justify-content: space-between;
    gap: 2rem;
    /* delete below */
/*     outline: 2px solid cyan;
 */}

.social-handles {
    width: 60px;
    height: 60px;
    padding: 0 0.875rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 0 solid #e5e7eb;
    border-radius: 50%;
    background-color: #e3e2e0;
    cursor: pointer;
}

.footer-right {
    margin: 0;
    width: 68%; 
    display: flex;
    justify-content:space-evenly;
    align-self: stretch;
/*     gap: 0.25rem; 
 */    margin-top: 0.25rem;

    /* delete below */
/*     outline: 3px dotted violet;
 */   /*  flex-wrap: wrap; */
}

.right-columns {
    width: 28%;
    display: flex;
    flex-direction: column;
    gap: 30px;
    /* delete below */
/*     outline: 3px solid burlywood;
 */    flex-basis: 20ch;
}

.right-columns-heading {
    margin: 0;
    padding: 0;
    color: #f54748;
    font-size: 25px;
    font-weight: 600;
}

.right-columns-list {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 1.25rem;
    list-style: none;
    color: #e3e1df;
    font-size: 20px;
    font-weight: 400;
}

.copyright {
    margin: 0;
    color: #e3e2e0;
    font-weight: 400;
    font-size: 20px;
    text-align: center;
}

.right-columns-list a{
    text-decoration: none;
    color: #e3e1df;
}

.right-columns-list a:hover {
    color: #f54748;
}

.right-columns-list a.active {
    padding: 0;
    margin: 0;
    min-width: 0;
    min-height: 0;
    width: fit-content;
    height: 27.20px;
    border: none;
    border-radius: 0;
    font-size: 20px; /* was 18px */
    font-weight: 400;
    background-color: inherit;
   /*  color: #f54748;
    border-bottom: 1px solid #f54748; */
}

@media screen and (max-width: 933px) {
    .footer-content .firstrow {
        display: block;
        width: 90%;
        margin: auto;
    }
    .footer-left{
        width: 100%;
    }
    .footer-icons{
        width: 50%;
        justify-content: space-between;
    }

    .footer-right{
        margin: 2rem 0;
        flex-wrap: wrap;
        gap: 2rem;
    }

    .right-columns {
        flex-grow: 1;
    }
 
}

