@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300..800;1,300..800&display=swap');



.tabpanel-container{
    display: flex;
    flex-direction: column;
    align-self: stretch;
    gap: 50px;
}


.tab-buttons {
    display: flex;
    gap: 2.75rem;
    flex-wrap: wrap;
    padding: 0;
    list-style: none;
}



