.tab-contents{
    display: flex;
    flex-direction: column;
    align-self: stretch;

    gap: 5vw;
}



/* .catagories {
    width: 100%;
} */


.catagory {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px,1fr));
    gap: 34px;
    /* delete below */
}





.card {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 34px;
    padding: 30px 2.25rem;
    /* padding-left: 2.25rem;
    padding-right: 2.25rem;
    padding-top: 30px;
    padding-bottom: 30px; */
    background-color: #ffffff;
    border-radius: 40px;
/*     font-family: Open Sans;
 */    /* added later */
    font-family: inherit;
}


.card-image {
/*     width: 100%;
 */    /* margin: 0 auto;
    min-width: 270px;
    min-height: 270px; */
    object-fit: cover;
    border-radius: 50%;
    /* below added later */
    height: 270px;  
    width: 270px;  

}

.card-content {
   /*  width: 100%;
    margin: 0 auto; */
    max-width: 286px;
    display: flex;
    flex-direction: column;
    align-items: center;
    /* below added later */
/*     gap: 2.5rem;
 */    height: 250px;
    justify-content: space-between;
}

.texts {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.625rem;
}

.card-largetext{
    margin: 0;
    color: #311f09;
    font-weight: 600;
    font-size: 30px;
}


.card-smalltext {
    margin: 0;
    color: #59442b;
    font-weight: 400;
    line-height: 200%;
    font-size: 14px;
    text-align: center;
}

.price-and-ordernow {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1.25rem;
   /*  margin-top: 30px;
    margin-left: 0.5rem;
    margin-right: 0.5rem; */
    margin: 0 0.5rem;
    
}

.price {
    margin: 0;
    color: #311f09;
    font-weight: 700;
    font-size: 25px;
}

.price-and-ordernow a {
    text-decoration: none;
}


    /* added later */

.popularmenu-ordernow-button {
    display: flex;
    justify-content: center;
}
.popularmenu-ordernow-button a {
    text-decoration: none;
}


@media (max-width: 833px) {
    .Active, .inActive {
        transform: scale(0.85);
        padding: 10px;
        min-width: 130px;
        height: 50px;
    }

    .tab-buttons {
       gap: 1rem;

    }

    .medium-button {
        transform: scale(0.8);
    }

    .popularmenu-container {
        margin-bottom: 80px;
    }
}


@media (max-width: 682px) {


    .catagory {
        grid-template-columns: 0.7fr;
        justify-content: center;
    }
   
}


@media (max-width: 470px) {

    .popularmenu {
        padding: 0.5rem;
        gap: 25px;
        margin-top: 10vw;
    }
    .tab-buttons {
        gap: 0.5rem;
    }
}

