.header-container{
    width: 100%; /* added later to avoid shift among homepage header and other page's headers */
    max-width: 1114px;
    margin: 0 auto;
    display: flex;
    /* gap: 50px; */
    justify-content: space-evenly;
    align-items: center;
    border: 0 solid #e5e7eb;
   
    
}

.header-horizontal-list {
    padding: 0;
    display: flex;
    gap: 50px;
    align-items: flex-start;
    align-self: flex-start;
    list-style: none;
    font-weight: 400;
    font-size: 18px;
    color: #311f09cc;
    font-family: inherit;
    white-space: nowrap;
}


.cart-and-login {
    display: flex;
    gap: 1.5rem;
}


.cartButton{
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    background-color: #ffffff;
    border:0 solid #e5e7eb;
    border-radius: 24px;
    cursor: pointer;
}


.header-horizontal-list a {
    text-decoration: none;
    color: #311f09cc;

}


.header-horizontal-list a:hover{
    color: #f54748;
}


.header-horizontal-list a.active {  /* overwriting aria-current:"page". */
    padding: 0;
    margin: 0;
    min-width: 0;
    min-height: 0;
    width: fit-content;
    height: 27.20px;
    border: none;
    border-radius: 0;
    font-size: 18px;
    font-weight: 400;
    background-color: inherit;
    color: #f54748;
    border-bottom: 1px solid #f54748;
}

.cart-and-login a{
    text-decoration: none;
}

.cart-and-login a.active {
    padding: 0;
    margin: 0;
    min-width: 0;
    min-height: 0;
    width: fit-content;
    height: fit-content;
    border: none;
    border-radius: 0;
    background-color: inherit;
    
}

.open-sidebar-button, .close-sidebar-button, #sidebar-active{
    display: none;
}


.header-vertical-list {
    position: absolute;     /* To take out of the document flow. */
    right: -100%;
    visibility: hidden;
}





@media (max-width:1024px) {

    .header-container{
        gap: 0; /* was 20px */
        transform: scale(0.9);
        
    }
    
     .header-horizontal-list{
        gap: 25px;
        transform: scale(0.95);
    }
    .cart-and-login {
        gap: 0.5rem;
        transform: scale(0.85); /* was 0.8 */
         
    }
    .header-login {
       transform: scale(0.9);
    }
}
    


@media screen and (max-width: 768px) {


.header-container{
    width: 100%;
    padding: 2px;
    justify-content: space-between;
    gap: 3rem;
 }

 .header-horizontal-list{
    display: none;
 }
 
 
 .cart-and-login{
    gap: 0.5rem;
    transform: scale(0.85); 
}

/*  .logo-container, .logo {
    transform: none;
 } */
 
 .open-sidebar-button {
    display: flex;
    padding: 10px;
    background-color: #ffffff;
    border:0 solid #e5e7eb;
    border-radius: 50%;
}



/* added later */


.open-sidebar-button img{          
    width: 30px;
    height: 30px;

}
.close-sidebar-button {
    display: flex;
    /* added later */
    width: 24px;
    height: 24px;
}


/* .close-sidebar-button {
    display: flex;
}
 */
 
#sidebar-active:checked ~ .header-vertical-list{
    margin: 0;
    padding: 0.5rem 3.2rem 0.5rem 0.875rem;
    visibility: visible;    /* previously set to hidden so I have to make it visible again. */
    display: flex;
    flex-direction: column;
    gap: 2rem;
    position: fixed;
    z-index: 10;
    top: 110px;
    right: 0;
    transition: 0.75s ease-out;
    /* background: transparent;
    backdrop-filter: blur(30px);  */
    background-color: #f9f9f9;
    box-shadow: 5px 10px 15px 10px rgb(0,0,0,10%);
/*     box-shadow: 0px 10px 15px 10px rgb(0 0 0 / 15%);
 */    /* background-color: rgb(228 228 228 / 15%); */
    border-radius: 16px 0 0 16px;
}
    

.header-vertical-list a {
    text-decoration: none;
    font-size: 18px;
    color: #311f09cc;
    

}

.header-vertical-list a:hover{
    color: #f54748;
}


.header-vertical-list a.active {         /* overwriting aria-current:"page". */
    padding: 0;
    margin: 0;
    min-width: 0;
    min-height: 0;
    width: fit-content;
    height: 27.20px;
    border: none;
    border-radius: 0;
    font-size: 18px; /* was 18px */
    font-weight: 400;
    background-color: inherit;
    color: #f54748;
    border-bottom: 1px solid #f54748;
}

}


 




    



