.logo-container {
    display: flex;
    flex-direction: row;
    gap: 10px;
    flex-wrap: nowrap;
    width: 124px;
    height: 50px;
}
.logo {
    background-color: #f54748;
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-weight: 600;
    font-size: 25px;
    color: #f9f9f9;
}

.s{
    transform: rotate(-17deg);

}
.logo-text{
   display: flex;
   align-items: center;
   font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
   font-weight: 600;
   font-size: 20px;
}

.period{
    color:#f54748
}