@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300..800;1,300..800&display=swap');

.hero-container {
    display: flex;
    justify-content: center;
    align-self: stretch;
    padding: 0 3rem;
    gap: 1.5rem; /* was 3.2rem */
    font-family: Open Sans;

}

.hero-content {
    max-width: 48%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 1.2rem;
    /* added later */
    align-items: center;

/*     outline: 2px solid blueviolet;
 */
}


.hero-largetext {
    margin: 0;
    padding: 0;
    color: #301e08;
    font-weight: 700;
   /*  line-height: 95px;
    font-size: 70px; */
    line-height: 130%;
    font-size: clamp(2.5rem,5vw,4rem);
    white-space: nowrap;
     /* delete below */
/*     outline: 3px dotted deepskyblue;
 */
    width: 90%;
}

.hero-smalltext {
    width: 80%;
    margin: 0;
    color: #5c4429;
    font-weight: 400;
    line-height: 180%;
/*     font-size: 20px;
 */  
    font-size: clamp(0.875rem,2.5vw,1.25rem);
   /* delete below */
/*     outline: 3px dotted darkred;
 */    
 width: 90%;
}




.hero-image{
    display: flex;
    align-items: center;
    max-width: 48%;
    min-width: 400px;   /* was width:400px height:450px */
    min-height: 450px;
    
/*     outline: 3px double blue;
 */}

.hero-illustration {

    object-fit: contain;
    width: 100%;
/*     outline: 3px dotted brown;
 */
}

.hero-order-button {        /* added later */
    width: 90%;
    
}

.hero-order-btn{
    display: none;
}

.hero-order-button a, .hero-order-btn a {
    text-decoration: none;
 }



 @media (max-width:900px) and (min-width:768px) {
 .hero-container{
    /* justify-content: space-between; */
    padding: 0.5vw; /* added later */
 }
/*  .hero-content, .hero-image{
    transform: scale(0.9);
 }
 .hero-illustration {
    width: 95%;
 }
 */
}



@media (max-width: 768px) {
    
.hero-order-button{
    display: none;
}


.hero-container{
    display: block;
    padding: 0 1rem;
    padding-top: 2vw;/* added later */
    text-align: center;
}

.hero-content{
    text-align: center;
    max-width: fit-content;
    padding: 0;
  
   
}

.hero-largetext{
    font-size: clamp(2.5rem,10vw ,3.5rem);

}


.hero-smalltext{
    width: 100%;
    font-size: clamp(1rem, 4vw, 1.25rem);
}


.hero-order-btn {
    display: flex;
    justify-content: center;
/*     transform: scale(0.8);
 */}



.hero-image{
    display: block;
    width: 100%;
    margin: 5vw auto;
    min-width: fit-content;
    min-height: fit-content;
}

.hero-illustration{
    width: 75%;
    height: 75%;
/*     margin: 5vw auto;
 */}

}