.features-container {
    display: flex;
    justify-content: space-between;
    align-self: stretch;
    padding: 5vw 3rem;
/*     gap: 3.2rem;
 */    font-family: Open Sans;
    background-color: #e7f7ed;
    margin-bottom: 125px;
}


.features-image {
    display: flex;
    align-items: center;
    max-width: 48%;
    min-width: 380px;   /* was width: 350px  height: 450px */
    min-height: 450px;
}


.features-illustration {
    object-fit: contain;
    width: 80%; /* was 80% */
}


.features-content{
    max-width: 48%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 1.8rem;
}

.features-largetext {
    margin: 0;
    padding: 0;
    color:#301e08;
    font-weight: 700;
    line-height: 130%;
    font-size: clamp(2.5rem,5vw,3.3rem);
    white-space: nowrap;
}

.features-smalltext{
    width: 80%;
    margin: 0;
    color: #5c4429;
    font-weight: 400;
    line-height: 180%;
    font-size: clamp(0.875rem,2.5vw,1.25rem);
}




.features-order-btn {
    display: none;
}

.features-order-button a, .features-order-btn a {
    text-decoration: none;
}



@media (max-width: 768px) {

   /*  .header-and-hero-container {
        margin-bottom: 10vw;
    } */
   
    .features-order-button{
        display: none;
    }
    
    
    
    .features-container{
        padding: 0 1rem;
        padding-top: 2.5rem;
        text-align: center;
        display: flex;
        flex-wrap: wrap;
        flex-direction: column-reverse;
        margin-bottom: 0;
    }
    
    .features-content{
        text-align: center;
        max-width: fit-content;
        padding: 0;
        display: flex;
       
    }
    
    .features-largetext{
/*         font-size: clamp(1.5rem,10vw ,2.5rem);
 */        font-size: clamp(2.5rem,8vw ,3rem);
    }
    
    
    .features-smalltext{
        width: 100%;
        font-size: clamp(1rem, 4vw, 1.25rem);
    }
    
    
    .features-order-btn {
        display: flex;
        justify-content: center;
/*         padding: 1.5rem 0;    
 */        
        padding-bottom: 1.5rem;
        background-color:#e7f7ed;
        margin-bottom: 10vw;

    }

    .large-button{                  /* Applies to all ButtonL for small screen ( < 768px ) */
        transform: scale(0.7);
    }
    
    .features-image{
        display: block;
        padding: 2rem 0; /* added later */
        width: 100%;
        margin: 0 auto;
        min-width: fit-content;
        min-height: fit-content;
        /* added later */
       /*  min-width: 350px;
        min-height: 400px; */
}
    
    .features-illustration{
        width: 65%; /* was 60% */
        height: 65%;
/*         margin: 5vw auto;
 */    }
    
}
