.orderplace-header {
    padding: 50px 0;
    background-image:linear-gradient(180deg, #f447482b, #f5474800);
}


.orderplace {
    max-width: 1114px;
    margin: 0 auto;
    display: flex;
    justify-content: center;
}

.orderplace-cart {
    width: 60%;
    margin: 5vw 0;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    align-self: flex-start;
    padding: 2.25rem 0;
/*     gap: 50px;
 */    border-radius: 20px;
    background-color: #ffffff;
    box-shadow: 0 4px 29px 0 #60676933;
}

.orderplace-cart h2 {
    align-self: center;
    font-size: 30px;
    font-weight: 500;
}

.customer-details {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    padding: 1rem 0;
    background-color: #28a745;
    margin-bottom: 0.875rem;
}

.customer-details p {
    margin: 0;
    padding: 0 1rem;
    font-weight: 400;
    color: #ffffff;
}

.orderplace-cart h4{
    margin: 0;
    font-weight: 500;
    font-size: clamp(18px,2vw,20px);
}

.orderplace-cart-item{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 0.875rem;
/*     font-size: clamp(10px, 5vw,20px);
 */    gap: 2vw;

}

.orderplace-cart-bill {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 0.25rem;
    border-top: 1px solid #d1cccc;
/*     font-size: clamp(10px, 5vw,20px);
 */}



@media (max-width:768px) {
    .orderplace-cart {
        width: 80%;
    }
}

@media (max-width:425px) {
    .orderplace-cart {
        width: 90%;
    }
}