.menu-header {
    padding: 50px 0;
    background-image:  linear-gradient(180deg, #f447482b, #f5474800);
}

.menu-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 10vw;
}


.menu {
    max-width: 1114px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 5vw;
    margin: 0 auto;
    padding: 1.5rem;
}

.menu h1 {
    font-family: inherit;
    font-size: clamp(2.3rem,6vw,4rem);
    font-weight: 400;
    margin: 0;
}
