.checkout-header {
    padding: 5vw 0;
    background-color: #ececec;
}

.checkout-container {
    display: flex;
    justify-content: center;
    font-family: inherit;
    background-color: #ececec;
}

.checkout {
    width: 100%;
    max-width: 1114px;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    margin: 5vw 0;
}

.checkout-form-container {
    width: 80%;
    display: flex;
    padding: 2.75rem;
    flex-direction: column;
    align-items: center;
    gap: 1.75rem;
    background-color: #ffffff;
    border-radius: 16px;
    box-shadow: 0 12px 10px 0 #00000007;
}

.checkout-form-container h1 {
    font-family: inherit;
    font-size: clamp(1.3rem,6vw,2.5rem);
    font-weight: 400;
    margin: 0;
}

.checkout-form-container p {
    margin: 0;
    font-size: 18px;
    font-weight: 500;
    color: #311f09;

}

.shipping-address {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    align-self: flex-start;
}

/* .shipping-address-input {
    display: flex;
    gap: 1rem;
    align-self: stretch;
} */

.address-field {
    height: 52px;
    padding: 0 1.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #ffffff;
    border: 1px solid #c4c4c4;
    border-radius: 0.5rem;
    font-size: 16px;
    font-family: inherit;
    color: #311f09;
}

.user-details {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    align-self: flex-start;
}

.user-details-fields {
    display: flex;
    gap: 1rem;
}

.user-field {
    width: 50%;
    height: 52px;
    padding: 0 1.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #ffffff;
    border: 1px solid #c4c4c4;
    border-radius: 0.5rem;
    font-size: 16px;
    font-family: inherit;
    color: #311f09;
}


.textarea {
    width: 100%;
    display: flex;
}

.textarea-field {
    width: 100%;
    height: 130px;
    padding: 1.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #ffffff;
    border: 1px solid #c4c4c4;
    border-radius: 0.5rem;
    font-size: 16px;
    font-family: inherit;
    color: #311f09;
}

.payment-method {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    align-self: flex-start;
}  

.payment-method-button {
    display: flex;
   /*  justify-content: space-between;
    align-items: center;
    gap: 0.875rem;
    flex-wrap: wrap; */
}

.payment-btn {
    /* height: 52px;
    padding: 0 1.5rem; */
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #ffffff;
    /* border: 1px solid #c4c4c4;
    border-radius: 0.5rem; */
    font-size: 18px;
    font-family: inherit;
    color: #311f09;
}

.payment-radio {
    transform: scale(1.8);
    margin: 0.875rem;
}
.ordernow {
    width: 100%;
    display: flex;
    justify-content: center;
}

.navlink-btn {
    display: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.errors {
    display: flex;
    justify-content: space-between;
    color: #f54748;
    font-size: 14px;
}




@media (max-width: 768px) {
    .checkout-form-container{
        transform: scale(0.9);
    }
    .address-field, .user-field, .textarea-field {
        font-size: 12px;
    }
    
}