@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300..800;1,300..800&display=swap');

.testimonials-parent {
    display: flex;
    justify-content: center;
    font-family: inherit;
    margin: 10vw 0;
}

.testimonials-container {
    max-width: 1114px;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    padding: 0 3.5rem;
}

.heading-and-testimonials {
    display: flex;
    flex-direction: column;
/*     justify-content: center;
 */    align-items: center;
    gap: 6vw;      /* was 42px */
}

.testimonials {
/*     margin-top: 20px;
 */ position: relative;
    height: 394px;
    width: 100%;
    background-color: #ffffff;
    border: 0 solid #ececec;
    border-radius: 16px;
    box-shadow:0 4px 11px 0 #0a0a0a0f;
    text-align: center;
}

.testimonials-logo {
    display: flex;
    justify-content: center;
    padding: 10px;
}

.buttons-and-comments {
    height: fit-content;
    margin: auto;
    position: absolute;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1.25rem;
    top: 40px; /* was 0 */
    bottom: 0;
   /*  left: 0;
    right: 0; */
}

.arrow {
    margin: 0 1vw;
    padding: 0 18px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    background-color: #ffffff;
    cursor: pointer;
    border: 0 solid #e5e7eb;
}


.arrow:hover{
   
   transform: scale(1.1);
}

.user-comments{
/*     height: fit-content;
 */    height: 200px;
/*     margin: auto 0;
 */    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
/*     font-family:Open Sans;
 */    color: #301e08;
    word-spacing: 2px;
    
}

.user-comments p {
    margin: 0;
}
.user-comments h4{
    background-color:#ececec;
    margin: 0;
    margin-top: 10px;
    padding: 10px;
    border: 0 solid #e5e7eb;
    border-radius: 10px;

}



