@import url('https://fonts.googleapis.com/css2?family=Tinos:ital,wght@0,400;0,700;1,400;1,700&display=swap');

.aboutus-header {
    padding: 50px 0;
    background-image:  linear-gradient(180deg, #f447482b, #f5474800);
}

.aboutus-parent {
    display: flex;
    flex-direction: column;
    gap: 4vw;
}

.aboutus-container {
    display: flex;
    justify-content: space-between;
    align-self: stretch;
    padding: 5vw 3rem;
/*     gap: 3.2rem;
 */    font-family: Open Sans;
/*     background-color: #e7f7ed;
 */    /* margin-bottom: 125px; */
}

 
.aboutus-image {
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 48%;
    min-width: 380px;   /* was width: 350px  height: 450px */
    min-height: 450px;
}




.aboutus-illustration {
    object-fit: contain;
    width: 80%; /* was 80% */ 
    border: 0 solid #ececec;
    border-radius: 24px;
   

}


.aboutus-chef-illustration {
    width: 65%;
    object-fit: contain;
    border: 0 solid #ececec;
    border-radius: 24px;
    justify-self: center;
}



.aboutus-content{
    max-width: 48%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 1.8rem;
}

.aboutus-largetext {
    margin: 0;
    padding: 0;
    color:#301e08;
    font-weight: 700;
    line-height: 130%;
    font-size: clamp(2rem,4.5vw,3.3rem);
    white-space: nowrap;
}

.aboutus-smalltext{
    width: 80%;
    margin: 0;
    color: #5c4429;
    font-weight: 400;
    line-height: 180%;
    font-size: clamp(14px,1.8vw,20px);
    font-family: inherit;
}


.aboutus-order-btn {
    display: none;
}

.chef-name{
    margin: 0;
    font-weight: 400;
    color: #5c4529;
    font-size: clamp(24px, 2vw, 40px);
}




@media (max-width: 768px) {

    /*  .header-and-hero-container {
         margin-bottom: 10vw;
     } */
    
     .aboutus-order-button{
         display: none;
     }
     
     
     
     .aboutus-container{
         padding: 0 1rem;
         padding-top: 2.5rem;
         text-align: center;
         display: flex;
         flex-wrap: wrap;
         flex-direction: column-reverse;
         margin-bottom: 0;
     }
     .aboutus-second-section {
        flex-direction: column;
     }
     
     .aboutus-content{
         text-align: center;
         max-width: fit-content;
         padding: 0;
         display: flex;
        
     }
     
     .aboutus-largetext{
 /*         font-size: clamp(1.5rem,10vw ,2.5rem);
  */        font-size: clamp(2.2rem,8vw ,3rem);
     }
     
     
     .aboutus-smalltext{
         width: 100%;
         font-size: clamp(1rem, 4vw, 1.25rem);
     }
     
     
     .aboutus-order-btn {
         display: flex;
         justify-content: center;
 /*         padding: 1.5rem 0;    
  */        
         padding-bottom: 1.5rem;
/*          background-color:#e7f7ed;
 */         /* margin-bottom: 10vw; */
 
     }
 
     .large-button{                  /* Applies to all ButtonL for small screen ( < 768px ) */
         transform: scale(0.7);
     }
     
     .aboutus-image{
         display: block;
         padding: 2rem 0; /* added later */
         width: 100%;
         margin: 0 auto;
         min-width: fit-content;
         min-height: fit-content;
         /* added later */
        /*  min-width: 350px;
         min-height: 400px; */
 }
     
     .aboutus-illustration{
         width: 65%; /* was 60% */
         height: 65%;
 /*         margin: 5vw auto;
  */    }
     
 }