@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300..800;1,300..800&display=swap');


.action-parent {
    display: flex;
    justify-content: center;
    font-family: Open Sans;
    margin-bottom: 10vw;
}

.action-container {
    max-width: 1114px;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    /* below added later */
   /*  background-color: #fdd9d9;
    border-radius: 24px;
    padding: minmax(6vw,8vw); */



}

.action-content {
/*     padding: 50px 180px;
 */    display: flex;
    min-height: 220px;
    flex-direction: column;
/*     justify-content: space-evenly;
 */    align-items: center;
    gap: 3vw;
    background-color: #fdd9d9;
    border-radius: 24px;
    padding: clamp(4vw, 6vw, 8vw) clamp(4vw, 8vw, 12vw);

}

.action-content h1 {
    font-size: clamp(2rem,5vw,3.5rem);
    white-space: nowrap;
}

.action-button{
    display: flex;
    justify-content: center;
}

.action-button a{
    text-decoration: none;
}



@media (max-width: 580px) {

    .action-content {
        transform: scale(0.7);
    }
}
   