.small-button {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    min-width: 112px;
    height: 50px;
    padding: 0 34px;
    background-color: #f54748;
    border: 0 solid #e5e7eb;
    border-radius: 16px;
    color: #ffffff;
    font-size: 14px;
    font-weight: 600;
    cursor: pointer;
}

.large-button {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    min-width: 232px;
    height: 74px;
    padding: 0 34px;
    background-color: #f54748;
    border: 0 solid #e5e7eb;
    border-radius: 16px;
    color:#ffffff;
    font-size: 20px;
    font-weight: 600;
    cursor: pointer;
}

.medium-button {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    min-width: 158px;
    height: 54px;
    padding: 0 34px;
    background-color: #f54748;
    border: 0 solid #e5e7eb;
    border-radius: 0.5rem;
    color: #ffffff;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
}

.inActive {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    min-width: 232px;
    height: 74px;
    padding: 0 54px;
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
    border: 0 solid #e5e7eb;
    border-radius: 16px;
    color: #311f09;
    font-size: 20px;
    line-height: 200%;
    font-weight: 400;
    cursor: pointer;
}


.Active {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    min-width: 232px;
    height: 74px;
    padding: 0 54px;
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
    border: 0 solid #e5e7eb;
    border-radius: 16px;
    color: #ffffff;
    font-size: 20px;
    line-height: 200%;
    font-weight: 400;
    cursor: pointer;
    background-color:#f54748;
}


.checkout-button {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 60px;
    padding: 0 6vw;
    border: 0 solid #e5e7eb;
    border-radius: 14px;
    color: #ffffff;
    font-size: 25px;
    font-weight: 600;
    cursor: pointer;
    background-color: #f54748;  
}