.orderonline-header {
    padding: 50px 0;
    background-image:  linear-gradient(180deg, #f447482b, #f5474800);
}

.orderonline-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: inherit;
    margin-bottom: 8vw; /* added later */
}



.orderonline-tabpanel {
    max-width: 1114px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 5vw;
    margin: 0 auto;
    padding: 1.5rem;
}



.orderonline-tabpanel h1 {
    font-size: clamp(2.3rem,6vw,4rem);
    font-weight: 400;
    margin: 0;
}


.orderonline-tabcontent-and-cart {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
}

.orderonline-tabcontent-and-cart h4{
    margin: 0;
    font-weight: 500;
    font-size: clamp(18px,2vw,20px);
}

.orderonline-tabcontent {
    width: 90%;
    display: grid;
    gap: 0.875rem;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
}

.itemcard {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 1.25rem;
    font-family: inherit;
    background-color: #ffffff;
    border-radius: 44px;
    gap: 0.875rem;
}


.itemcard-image {
    width: 172px;
    height: 172px;
    object-fit: cover;
    border-radius: 50%;
}

.itemcard-content {
    max-width: 286px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 300px;
    justify-content: space-between;
}

.itemcard-texts { 
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.25rem;
}

.itemcard-largetext{
    margin: 0;
    color: #311f09;
    font-weight: 600;
    font-size: 20px;
}


.itemcard-smalltext {
    margin: 0;
    color: #59442b;
    font-weight: 400;
    line-height: 200%;
    font-size: 14px;
    text-align: center;
}


.itemcard-price-and-signs {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
}

.signs-and-quantity {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1.25rem;
    border-radius: 16px;
    background-color: #fafaf9;
}

.signs-and-quantity p{
    font-size: 16px;
    font-weight: 400;
    color: #311f09;
    margin: 0;
}

.minus-sign {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 16px;
    background-color: #ffffff;
    padding: 0.875rem 0.625rem;
    box-shadow: 3.32px 3.32px 4px 0 #ff4e4e11;
    cursor: pointer;
    border: none;
}

.plus-sign {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 16px;
    background-color: #ffffff;
    padding: 0.625rem;
    box-shadow: 3.32px 3.32px 4px 0 #ff4e4e11;
    cursor: pointer;
    border: none;

}


.orderonline-cart {
    min-width: 32%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    align-self: flex-start;
    padding: 2.25rem 0;
    gap: 50px;
    border-radius: 20px;
    background-color: #ffffff;
    box-shadow: 0 4px 29px 0 #60676933;
}


.orderonline-cart h2 {
    align-self: center;
    font-size: 30px;
    font-weight: 600;
}



.cart-content {
    width: 100%;
    display: flex;          
    flex-direction: column;
    align-items: flex-start;
    gap: 4rem;
    /*margin: 0 30px;*/
}

.cart-food-items {
    width: 100%;
    max-height: 50vh;
    display: flex;
    flex-direction: column;
    gap: 0.25rem;
    border-top: 1px solid #d1cccc;
    overflow-y: scroll;
}


.cart-item {  
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 0.875rem;
    gap: 2vw;
}

.item-and-delete {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
/*     gap: 1rem;
 */}

.deletebtn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 24px;
    height: 24px;
    background-color: #ffffff;
    border: none;
    cursor: pointer;
}

.cart-signs-and-price {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.cart-bill {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 0.25rem;
    border-top: 1px solid #d1cccc;
}

.bill-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.875rem;
}



.checkout-btn {
    margin: auto;
}

.checkout-btn a {
    text-decoration: none;
   /*  padding: 0;
    margin: 0;
    min-width: 0;
    min-height: 0;
    width: fit-content;
    height: 27.20px;
    border: none;
    border-radius: 0;
    font-size: 18px;
    font-weight: 400;
    background-color: inherit;
    color: #f54748;
    border-bottom: 1px solid #f54748; */
}


@media (max-width:768px) {
    .orderonline-tabcontent-and-cart {
        display: block;
    }

    .orderonline-tabcontent {
        width: 100%;
        margin: auto;
    }

    .orderonline-cart {
        margin: 3vw 0;
        transform: scale(0.95);
    }
}
